:root{
    --header-height: 3rem;
    --nav-width:78px;

    --sidebar-color: #F7F7FA;
    --first-content-color: #FFFFFF;
    --font-color: #18AEFA;
    --listItem-color: #646263;
}

a{
    text-decoration: none;
}

ul{
    list-style: none;
}
#sidebar{
    position: relative;
    margin: 3rem 0 0 0;
    margin: var(--header-height) 0 0 0;
    padding: 0 .88rem;
    font-size: 1rem;
    transition: 0.5s;
    
}
.header{
    position: fixed;
    top: 0;
    left:0;
    height: 3rem;
    height: var(--header-height);
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #FFFFFF;
    background-color: var(--first-content-color);
    padding: 0 1rem;
    transition: .5s;
    border-bottom: 1px solid #99999980;
}

.headerToggle{
    font-size: 1.25em;
    cursor: pointer;
    color: #FFFFFF;
    color: var(--first-content-color);
}
#headerIcon{
    color: #646263;
    color: var(--listItem-color);
    font-size: 1.56rem;
    color: #02a9fc;
}
.sidebar{
    position: fixed;
    top: 0;
    bottom: 0;
    overflow-y: scroll;
    left: -30%;
    height: auto;
    width: 78px;
    width: var(--nav-width);
    background-color: #F7F7FA;
    background-color: var(--sidebar-color);
    padding-top: 1rem;
    transition:0.5s;
}

/* SIDEBAR SUB MENU STYLES START */

.sidebar-item{
    /* padding: .75em 1em; */
    display: block;
    justify-content: space-between;
    align-items: center;
    transition: background-color .15s;
    border-radius: 5px;
  }
  .sidebar-item:hover{
    background-color: #ffffff1a;
  }
  
  .sidebar-title{
    display: flex;
    font-size: 1em;
    justify-content: space-between;
    align-items: center;
  }

  .sidebar-title span i{
    display: inline-block;
    width: 1.5em;
  }
  .sidebar-title .toggle-btn{
    margin-left: -1rem !important;
    cursor: pointer;
    transition: -webkit-transform .3s;
    transition: transform .3s;
    transition: transform .3s, -webkit-transform .3s;
  }
  .sidebar-item.open > .sidebar-title .toggle-btn{
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
  }
  .sidebar-content{
    padding-top: .25em;
    height: 0;
    overflow: hidden;
  }
  .sidebar-item.open > .sidebar-content{
    height: auto;
  }
  /* SIDEBAR TEACHER */
 
 
  /* SIDEBAR TEACHER END */
  .sidebar-submenu{
      margin-bottom: 1rem;
  }
#navLinkIcon{
    margin-right: 2rem !important;
}
/* SIDEBAR SUB MENU STYLES END */


/* sidebar scrollbar style */
.sidebar::-webkit-scrollbar {
    width: 5px;
    height: 10px;
    opacity: 0;
}
.sidebar::-webkit-scrollbar-button {
    display: none;
}

.sidebar::-webkit-scrollbar-thumb {
    border-radius: 50px;
    background: #E6E6E8;
}

.sidebar::-webkit-scrollbar-track {
    border-radius: 50px;
}

.sidebar::-webkit-scrollbar {
    display: none;
}

.sidebar:hover::-webkit-scrollbar {
    display: block;
    background: fff;
}


/* nav item style */
.nav{
    display: flex;
    flex-direction:column;
    justify-content:space-between;
    /* overflow: hidden; */
    height: 100%;
    position: relative;        
}

.navLogo,.navLink {
    display: grid;
    grid-template-columns: -webkit-max-content -webkit-max-content;
    grid-template-columns: max-content max-content;
    grid-column-gap: 2rem;
    -webkit-column-gap: 2rem;
            column-gap: 2rem;
    padding: 0.5rem 0 0.5rem 1.5rem;
  
}
.navLogo{
    margin-bottom: 2rem;
    font-weight: 700;
    color: #646263 !important;
    color: var(--listItem-color) !important;
}

#navLogoIcon{
    font-size: 1.25rem;
    color: #646263;
    color: var(--listItem-color);
    cursor: pointer;
}
.navLinkName{
    font-weight: 700;
}

.navLink{
    position: relative;
    color: #646263;
    color: var(--listItem-color);
    transition: 0.5s;
    margin-bottom: 1.5rem;
    font-weight: 700;
}
#navLinkIcon{
    margin-right: 1rem;
}
.navLink:hover{
    color: #18AEFA;
    color: var(--font-color);
    background-color: #FFFFFF !important;
    background-color: var(--first-content-color) !important;
}
.dropdown-menu{
    background-color: #F7F7FA;
    background-color: var(--sidebar-color);
}
.headerImgContainer{
    width: 5%;
    height: 3rem;
    display: flex;
    margin: 3rem;
    align-items: center;
}
.topleft{
    display: flex;
    justify-content: center;
    align-items: center;
}
 .bell{
    font-size: 1.56rem;
    color: #646263;
    color: var(--listItem-color);
    margin-right: 1rem;
}
.headerImg{
    width:60%;
    height: 60%;
    border-radius: 50%;
    object-fit: cover;
}
#active{
    color: #646263 !important;
    color: var(--listItem-color) !important;
    background-color: #F7F7FA !important;
    background-color: var(--sidebar-color) !important;
    font-weight: 600;

}
#active:hover{
    color:#02a9fc !important;
}
#active::before{
    content: " ";
    position: absolute;
    top: 0;
    left: 0;
    height: 39px;
    width: 4px;
    background-color: #18AEFA;
    background-color: var(--font-color);
    transition: 0.5s;
}
 .show{
    left: 0%;
} 
/* #sidebar:hover .show {
    width: calc(var(--header-height) + 156px)
} */
.spaceToggle{
    padding-left: calc(78px + 1rem);
    padding-left: calc(var(--nav-width) + 1rem);
}
.content{
    display: flex;
    justify-content: center;
    align-items: center;
}
.welcome{
    margin-top: 7rem;
    letter-spacing: .3rem;
    text-transform: uppercase;
    color: #18AEFA;
    color: var(--font-color);
    border: 1px solid #333;
}

/* dropdown menus styles */


@media only screen and (min-width: 768px) {
    #sidebar{
        margin: calc(3rem + 1rem) 0 0 0;
        margin: calc(var(--header-height) + 1rem) 0 0 0;
        padding: 0 1rem 0 calc(78px + .2rem);
        padding: 0 1rem 0 calc(var(--nav-width) + .2rem);
    }
    .header{
        height: calc(3rem + 1rem);
        height: calc(var(--header-height) + 1rem);
        padding-left: calc(78px + 2rem);
